<template>
    <div id="app">

        <app-header></app-header>

        <router-view :page_data="page_data"></router-view>

        <app-footer></app-footer>

    </div>
</template>

<script>
import Header from './components/Header';
import Footer from './components/Footer';
import axios from 'axios';

var hebrew_api = 'https://accessibility-helper.co.il/hebrew-api-json/';

export default {
    name: 'app',
    data(){
        return {
            page_data: {}
        }
    },
    components : {
        appHeader: Header,
        appFooter: Footer
    },
    mounted(){
        axios.get(hebrew_api).then(function (response) {
            this.page_data = response.data;
            console.log(response.data);
        }.bind(this));
    }
}
</script>



